import { Util } from './../../utils';
import { CasesService } from './../../services/cases/cases.service';
import { Component, OnInit } from '@angular/core';
import { CasesGraph } from 'src/app/models/cases-graph.model';
import { Cases } from 'src/app/models/cases.model';

@Component({
  selector: 'app-daily-cases',
  templateUrl: './daily-cases.component.html',
  styleUrls: ['./daily-cases.component.css']
})
export class DailyCasesComponent implements OnInit {

  private _casesGraphList: Array<CasesGraph>;
  private casesList: Array<Cases>;

  constructor(private casesService: CasesService,
    private util: Util) { }

  ngOnInit(): void {
    this.casesService.getAll().subscribe(
      (casesList: Array<Cases>) => {
        this.casesGraphList = casesList.map(cases => new CasesGraph(cases.date, cases.confirmedCases, cases.deathCases, cases.recoveredCases));
        this.casesList = casesList;
      }, 
      () => {
        this.util.showSnackBar('Greška! Informacije o covid19 virusu u Srbiji trenutno nisu dostupne. Molimo vas pokušajte kasnije.', 6, false);
      }
    )
  }

  get casesGraphList(): Array<CasesGraph> {
    return this._casesGraphList;
  }

  set casesGraphList(casesGraphList: Array<CasesGraph>) {
    this._casesGraphList = casesGraphList;
  }
}
