export class CasesGraph {
    date: Date;
    active: number;
    confirmed: number;
    deaths: number;
    recovered: number;

    constructor(date: Date, confirmed: number, deaths: number, recovered: number) {
        this.date = date;
        this.active = confirmed - deaths - recovered;
        this.confirmed = confirmed;
        this.deaths = deaths;
        this.recovered = recovered;
    }
}